.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.auth-card {
  width: 100%;
  max-width: 400px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.auth-title {
  text-align: center;
  margin-bottom: 30px;
  color: #1890ff;
}

.auth-switch {
  display: flex;
  justify-content: center;
  margin-top: 16px;
}
